<template>
  <div
    class=" max-w-xs rounded shadow-Card hover:shadow-CardHover bg-white-100 mx-4 my-4 md:max-w-sm2"
  >
    <img
      v-if="imgsrc != ''"
      class="w-full h-auto max-h-full cursor-pointer"
      :src="imgsrc"
      alt="活動圖片"
      @click="IsPop = true"
    />
    <div class="px-4 py-2 md:px-6 md:py-4">
      <div class="text-black-800 font-bold text-xl mb-2 text-left">
        {{ name }}
      </div>
      <p class="text-gray-700 text-base font-semibold break-all text-justify">
        {{ context }}
      </p>
    </div>
    <div class="px-6 py-4">
      <span
        v-for="(item, index) in tags"
        :key="index"
        class="inline-block bg-purple-600 rounded-full px-3 py-1 text-sm font-semibold text-yellow-100 mr-2"
        ># {{ item }}</span
      >
    </div>
    <template v-if="IsPop">
      <div
        class="fixed w-screen h-full inset-0 z-50 bg-black-opacity-75 cursor-pointer"
        @click="IsPop = false"
      >
        <img
          class="my-16 mx-auto block max-h-full cursor-default"
          :src="imgsrc"
          alt="活動圖片"
          @click.stop=""
        />
      </div>
    </template>
  </div>
</template>

<script>
export default {
  name: "Card",
  props: ["name", "context", "imgsrc", "tags"],
  data() {
    return {
      IsPop: false
    };
  }
};
</script>

<style></style>
